export const isValidAuthorImage = (url) => {
    if (!url) return false;
    if (url === "" || url.includes('ui-avatars')) return false;
    else return true;
}

const updateStatusRenewImage = (wallId, postId, e, dataTB) => {
    axios.post(`https://app.taggbox.com/widget/post/updateStatus`, {
        wallId: wallId,
        postId: postId,
        table_name: dataTB
    }).then(({ data }) => {
        e.target.src = data.media;
        e.target.attributes["data-load"].value = "1";
    })
}

export const IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP = async (e) => {
    e.persist()
    // e.target.src = "https://media.geeksforgeeks.org/wp-content/uploads/20200617121258/gfg-image2-300x177.png"
    const network = e.target.getAttribute("data-network")
    const type = e.target.getAttribute("data-type");
    const wallId = e.target.getAttribute("data-wall-id");
    const postId = e.target.getAttribute("data-item-id");
    const filterId = e.target.getAttribute("data-filter-id");
    const dataStories = e.target.getAttribute("data-stories");
    const dataTB = e.target.getAttribute("data-tb");

    let image_update = '';
    let video_update = '';
    if (network) {
        const dataSrc = e.target.src;
        if (dataSrc.includes("no-camera.svg")) {
            e.target.style = "visibility:visible!important;width:180px;margin:20px auto;object-fit: contain;";
        }
        if ((network == 2 || network == 18) && e.target.getAttribute("data-load") != 1 && filterId != "34" && dataStories == 0) {
            if (dataSrc.includes("cdn.taggbox.com") || dataSrc.includes("no-camera.svg") && e.target.getAttribute("data-load") == 0) {
                if ((network == 2 || network == 18)) {
                    let links = e.target.getAttribute("data-link");
                    let postLinkId = links.split('/');
                    let fetchUrl = `https://images${~~(Math.random() * 3333)}-focus-opensocial.googleusercontent.com/gadgets/proxy?container=none&url=${links}`;
                    let IMG_URL_UPDATE = await FETCH_LINK(fetchUrl, postLinkId, dataSrc, 'image')
                    if (IMG_URL_UPDATE && IMG_URL_UPDATE != null && IMG_URL_UPDATE.length > 0 && typeof IMG_URL_UPDATE === 'string' && (IMG_URL_UPDATE.includes("no-camera.svg") || IMG_URL_UPDATE.includes("lookaside.instagram.com"))) {
                        updateStatusRenewImage(wallId, postId, e, dataTB)
                    }
                    else {
                        if (IMG_URL_UPDATE && IMG_URL_UPDATE != null && IMG_URL_UPDATE.length > 0 && typeof IMG_URL_UPDATE === 'string') {
                            e.target.src = `https://images.taggbox.com/${IMG_URL_UPDATE}`;
                            image_update = IMG_URL_UPDATE;
                            e.target.attributes["data-load"].value = "1";
                            if (IMG_URL_UPDATE.includes("no-camera.svg")) {
                                e.target.style = "visibility:visible!important;width:180px;margin:20px auto;object-fit: contain;";
                            }
                            e.target.style = "";
                        } else {
                            if (IMG_URL_UPDATE.display_url && IMG_URL_UPDATE.display_url.includes("lookaside.instagram.com")) {
                                updateStatusRenewImage(wallId, postId, e, dataTB)
                            }
                            else {
                                e.target.src = `https://images.taggbox.com/${IMG_URL_UPDATE.display_url}`;
                                image_update = IMG_URL_UPDATE.display_url;
                                video_update = IMG_URL_UPDATE.video_url;
                                e.target.attributes["data-load"].value = "1";
                                if (IMG_URL_UPDATE.includes("no-camera.svg")) {
                                    e.target.style = "visibility:visible!important;width:180px;margin:20px auto;object-fit: contain;";
                                }
                                e.target.style = "";
                            }
                        }
                        if (!image_update.includes("no-camera.svg") && !image_update.includes("cloud.taggbox.com") && !image_update.includes("cdn.taggbox.com") && !image_update.includes("lookaside.instagram.com")) {
                            updateInstagramPostImage({
                                wallId: wallId,
                                postFile: image_update,
                                video: video_update,
                                type: 2,
                                postId: postId,
                                table_name: dataTB
                            });
                        }
                    }
                }
                else {
                    axios.post(`https://app.taggbox.com/widget/post/updateStatus`, {
                        wallId: wallId,
                        postId: postId,
                        table_name: dataTB
                    }).then(({ data }) => {
                        // console.log('yes it is getting done');
                        // console.log(data)
                        e.target.src = data.media;
                        image_update = data.media;;
                        video_update = data.video;;
                        e.target.attributes["data-load"].value = "1";
                        // if (IMG_URL_UPDATE.includes("no-camera.svg")) {
                        //     e.target.style = "display:block;min-height:150px;width:80px;height:100%;margin:10px auto;object-fit: contain;";
                        // }
                    })
                }

            }
            else if (type === 'video') {

                let links = e.target.getAttribute("data-link");
                let postLinkId = links.split('/');
                let fetchUrl = `https://images${~~(Math.random() * 3333)}-focus-opensocial.googleusercontent.com/gadgets/proxy?container=none&url=${links}`;
                let IMG_URL_UPDATE = await FETCH_LINK(fetchUrl, postLinkId, dataSrc, 'video')

                if (IMG_URL_UPDATE && IMG_URL_UPDATE != null && IMG_URL_UPDATE.length > 0 && typeof IMG_URL_UPDATE === 'string') {
                    e.target.src = `https://images.taggbox.com/${IMG_URL_UPDATE}`;
                    e.target.attributes["data-load"].value = "1";
                    if (IMG_URL_UPDATE.includes("no-camera.svg")) {
                        e.target.style = "visibility:visible!important;width:180px;object-fit: contain;";
                    }
                    e.target.style = "";
                } else if (typeof IMG_URL_UPDATE === 'object' && type === 'video') {
                    e.target.src = `https://images.taggbox.com/${IMG_URL_UPDATE.video_url}`;
                    e.target.attributes["data-load"].value = "1";
                    if (IMG_URL_UPDATE.display_url.includes("no-camera.svg")) {
                        e.target.style = "visibility:visible!important;width:180px;object-fit: contain;";
                    }
                    e.target.style = "";
                }

            }

        }
        else {
            if (dataSrc.includes("no-camera.svg") && type === 'video') {
                e.target.style = "visibility:visible!important;width:180px;object-fit: contain;";
            }
            else if (dataSrc.includes("no-camera.svg") && type != 'video') {
                e.target.style = "visibility:visible!important;width:180px;margin:20px auto;object-fit: contain;";
            }
            else {
                e.target.src = NO_CAMERA_IMAGE;
                e.target.style = "visibility:visible!important;width:180px;margin:20px auto;object-fit: contain;";
            }
        }

    }
    console.clear();
}
