export const getHiddenContenetForPopUp = (currentIndex, eventData, completeData) => {
    let itsCurrentIndex = 0;
    if (currentIndex === -1) itsCurrentIndex = eventData.idArray.length - 1;
    else itsCurrentIndex = eventData.idArray.length == currentIndex ? 0 : currentIndex;

    let data = null;
    if (itsCurrentIndex === -1) data = completeData[eventData.idArray.length - 1];
    else data = completeData[itsCurrentIndex];
    if (data && Object.keys(data).length > 0) return data.content;
    else return "";
}

