import React, { Component, Fragment } from 'react';
import { RWebShare } from "react-web-share";
import { isMobile } from "react-device-detect";
class StoryPopupAuthor extends Component {
    state = {
        isActive: false
    }
    shareIconToggle = () => {

        if (this.state.isActive) {
            const { onAnimationON } = this.props;
            onAnimationON()
        }
        var tb_sp_share_list_container = document.querySelector(`#tb_sp_share_list_container`)
        if (tb_sp_share_list_container) {
            if (this.state.isActive && !tb_sp_share_list_container.classList.contains("tb_sp_toggle")) {
                tb_sp_share_list_container.classList.add('tb_sp_toggle');
                //console.log("check", tb_sp_share_list_container.classList.contains("tb_sp_toggle"))
                const { onAnimationPaused } = this.props;
                onAnimationPaused();
            }
            else {
                this.setState({ isActive: !this.state.isActive });
            }
        }
        else {
            this.setState({ isActive: !this.state.isActive });
        }

        //onAnimationON
    };

    updateAnimationPaused = event => {

        //this.shareIconToggle()
        var tb_sp_share_list_container = document.querySelector(`#tb_sp_share_list_container`)
        if (tb_sp_share_list_container) {
            if (tb_sp_share_list_container.classList.contains("tb_sp_toggle")) {
                tb_sp_share_list_container.classList.remove('tb_sp_toggle');
                const { onAnimationON } = this.props;
                onAnimationON()
            }
            else {
                tb_sp_share_list_container.classList.add('tb_sp_toggle');
                const { onAnimationPaused } = this.props;
                onAnimationPaused();
            }
        }
    }
    render() {
        const { isActive } = this.state;
        const { author, share, link, postAuthor, onAnimationON, wall } = this.props;
        const shareBody = `Hello there ! ${author.name} has sent you a social media post via Taggbox. Tap on the link below for a quick view. \n ${link}.`
        console.log(this.props)

        return <div className="tb_sp_content">
            <div className="tb_sp_user">
                <div className="tb_sp_user_profile">
                    {!(postAuthor == 0 || author.isInstaUser && wall.Wall.owner !== 100231) ? <img className="tb_sp_user_img" src={author.picture} onError={(e) => e.target.src = author.errorPic}></img> : null}
                </div>
                <div className="tb_sp_username">
                    {!(postAuthor == 0 || author.isInstaUser && wall.Wall.owner !== 100231) ? author.name : ""}
                </div>
            </div>
            <div className="tb_sp_share_post">
                <div className="tb_sp_share_wrap">
                    {share && share.status ? <div className="tb_sp_share_btn" onMouseDown={this.updateAnimationPaused}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="18" height="4" viewBox="0 0 18 4">
                            <g class="share_icon" data-name="share" transform="translate(-3 -10)">
                                <circle fill="#fff" className="Oval-67" cx="2" cy="2" r="2" transform="translate(3 10)" />
                                <circle fill="#fff" className="Oval-67-Copy" cx="2" cy="2" r="2" transform="translate(10 10)" />
                                <circle fill="#fff" className="Oval-67-Copy-2" cx="2" cy="2" r="2" transform="translate(17 10)" />
                            </g>
                        </svg>
                    </div>
                        : null}
                    <div id="tb_sp_share_list_container" className={`tb_sp_share_list ${isActive ? 'tb_sp_toggle' : ''}`}>
                        <a className="tb_sp_share_items" href={share.facebook} target="_blank" onClick={onAnimationON}>
                            <svg version="1.1" height="50" width="50" id="Capa_1" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 167.657 167.657">
                                <g>
                                    <path fill="#1777F2" d="M83.829,0.349C37.532,0.349,0,37.881,0,84.178c0,41.523,30.222,75.911,69.848,82.57v-65.081H49.626
                                                v-23.42h20.222V60.978c0-20.037,12.238-30.956,30.115-30.956c8.562,0,15.92,0.638,18.056,0.919v20.944l-12.399,0.006
                                                c-9.72,0-11.594,4.618-11.594,11.397v14.947h23.193l-3.025,23.42H94.026v65.653c41.476-5.048,73.631-40.312,73.631-83.154
                                                C167.657,37.881,130.125,0.349,83.829,0.349z"/>
                                </g>
                            </svg>
                            <div className="tb_sp_share_txt">
                                Facebook
                                    </div>
                        </a>
                        <a className="tb_sp_share_items" href={share.twitter} target="_blank">
                            <svg version="1.1" width="50" height="50" id="Capa_1" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 512 512">
                                <path fill="#03A9F4" d="M512,97.248c-19.04,8.352-39.328,13.888-60.48,16.576c21.76-12.992,38.368-33.408,46.176-58.016
                                            c-20.288,12.096-42.688,20.64-66.56,25.408C411.872,60.704,384.416,48,354.464,48c-58.112,0-104.896,47.168-104.896,104.992
                                            c0,8.32,0.704,16.32,2.432,23.936c-87.264-4.256-164.48-46.08-216.352-109.792c-9.056,15.712-14.368,33.696-14.368,53.056
                                            c0,36.352,18.72,68.576,46.624,87.232c-16.864-0.32-33.408-5.216-47.424-12.928c0,0.32,0,0.736,0,1.152
                                            c0,51.008,36.384,93.376,84.096,103.136c-8.544,2.336-17.856,3.456-27.52,3.456c-6.72,0-13.504-0.384-19.872-1.792
                                            c13.6,41.568,52.192,72.128,98.08,73.12c-35.712,27.936-81.056,44.768-130.144,44.768c-8.608,0-16.864-0.384-25.12-1.44
                                            C46.496,446.88,101.6,464,161.024,464c193.152,0,298.752-160,298.752-298.688c0-4.64-0.16-9.12-0.384-13.568
                                            C480.224,136.96,497.728,118.496,512,97.248z" fill="#1D9BEF" />
                            </svg>
                            <div className="tb_sp_share_txt">
                                Twitter
                                    </div>
                        </a>
                        <a className="tb_sp_share_items" href={link} target="_blank">
                            <svg xmlns="http://www.w3.org/2000/svg" id="tb__instagram__" width="24" height="24" viewBox="0 0 118.688 118.688">
                                <defs>
                                    <linearGradient id="linear-gradient" x1="0.5" y1="0.997" x2="0.5" y2="0.008" gradientUnits="objectBoundingBox">
                                        <stop offset="0" stop-color="#e09b3d" />
                                        <stop offset="0.3" stop-color="#c74c4d" />
                                        <stop offset="0.6" stop-color="#c21975" />
                                        <stop offset="1" stop-color="#7024c4" />
                                    </linearGradient>
                                    <linearGradient id="linear-gradient-2" y1="1.461" y2="-0.452" href="#linear-gradient" />
                                    <linearGradient id="linear-gradient-3" y1="6.581" y2="-1.4" href="#linear-gradient" />
                                </defs>
                                <g id="instagram-2" data-name="instagram">
                                    <path id="Shape" d="M83.33,0H35.358A35.4,35.4,0,0,0,0,35.358V83.33a35.4,35.4,0,0,0,35.358,35.358H83.33A35.4,35.4,0,0,0,118.688,83.33V35.358A35.4,35.4,0,0,0,83.33,0Zm23.418,83.33A23.418,23.418,0,0,1,83.33,106.748H35.358A23.418,23.418,0,0,1,11.94,83.33V35.358A23.418,23.418,0,0,1,35.358,11.94H83.33a23.418,23.418,0,0,1,23.418,23.418V83.33Z" fill="url(#linear-gradient)" />
                                    <path id="Shape-2" data-name="Shape" d="M42.262,11.565a30.7,30.7,0,1,0,30.7,30.7A30.732,30.732,0,0,0,42.262,11.565Zm0,49.454A18.757,18.757,0,1,1,61.019,42.262,18.757,18.757,0,0,1,42.262,61.019Z" transform="translate(17.082 17.082)" fill="url(#linear-gradient-2)" />
                                    <circle id="Oval" cx="7.356" cy="7.356" r="7.356" transform="translate(82.745 21.522)" fill="url(#linear-gradient-3)" />
                                </g>
                            </svg>
                            <div className="tb_sp_share_txt">
                                Instagram
                                    </div>
                        </a>
                        {!isMobile ? <a className="tb_sp_share_items" href={`mailto:?subject=You%20Got%20A%20Social%20Post%20-%20Taggbox%20Widget&body=${encodeURIComponent(shareBody)}`} target="_blank">
                            <svg xmlns="http://www.w3.org/2000/svg" width="30.383" height="21.943" viewBox="0 0 30.383 21.943">
                                <g id="Stockholm-icons-_-Communication-_-Mail" transform="translate(-3 -6)">
                                    <path id="Combined-Shape" d="M6.376,6H30.007a3.376,3.376,0,0,1,3.376,3.376V24.567a3.376,3.376,0,0,1-3.376,3.376H6.376A3.376,3.376,0,0,1,3,24.567V9.376A3.376,3.376,0,0,1,6.376,6ZM28.563,9.105,18.191,14.69,7.82,9.105a1.266,1.266,0,0,0-1.2,2.229l10.972,5.908a1.266,1.266,0,0,0,1.2,0l10.972-5.908a1.266,1.266,0,0,0-1.2-2.229Z" transform="translate(0 0)" fill="#2b2b2b" fill-rule="evenodd" />
                                </g>
                            </svg>
                            <div className="tb_sp_share_txt">
                                Mail
                                    </div>
                        </a> : null}

                        {isMobile ? <RWebShare
                            data={{
                                url: link,
                                title: "You Got A Social Post - Taggbox Widget",
                            }}
                            sites={["whatsapp", "reddit", "telegram", "linkedin", "mail", "copy"]}
                            onClick={onAnimationON}
                        >
                            <a className="tb_sp_share_items" target="_blank">
                                <svg xmlns="http://www.w3.org/2000/svg" width="48.014" height="42" viewBox="0 0 48.014 42">
                                    <path id="share___tb" data-name="Union 1" d="M23,42a1.383,1.383,0,0,1-1.2-1.1L16.3,18.3.4,2.7A1.791,1.791,0,0,1,.1,1,1.871,1.871,0,0,1,1.5,0h45a1.559,1.559,0,0,1,1.3.8,1.2,1.2,0,0,1-.1,1.5l-23.2,39a1.71,1.71,0,0,1-1.3.7ZM18,15.7,40.7,3.1H5.2Z" fill="#2b2b2b" />
                                </svg>

                                <div className="tb_sp_share_txt">
                                    Share
                                    </div>
                            </a>
                        </RWebShare> : null}

                    </div>
                </div>
            </div>
        </div >
    }
}
export default StoryPopupAuthor;