import React from 'react';
const AuthorAvatar = ({ username, color, style, textColor, networkId }) => {
    return <>
        <div className="author-avatar-block" style={{ ...style, backgroundColor: color, fontFamily: 'Open Sans', fontWeight: 500, color: networkId === null ? textColor : null  }}>
            {(username && username.length > 0) && username.charAt(0).toUpperCase()}
        </div>
    </>
}

export default AuthorAvatar;