import React, { Component } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { WINDOW_ON_LOAD } from './common/constent'
import WidgetPopUP from './component/WidgetPopUP'
import StoryPopup from './component/storyPopup/StoryPopup';
// import SubmitPic from './component/submitPic'
import OnsiteUpload from './component/onsiteupload'
class App extends Component {
  state = {
    isEnable: 0,
    eventData: {},
    shover: false,
    shareText: "SHARE",
    viewOnText: "View on",
    currentIndex: null,
    completeData: {},
    personalization: {},
    url: null,
    platform: null, imgData: [],
    themeRule: null,
    wall: null,
    type: "",
    csrf_token: null,
    iframeId: null
  }
  /* FEATURED POP UP CLOSE AND SEND TO CLOSE EVENT TO PARENT */
  closeWidgetPopUP = event => {
    const { windowParent, iframeId, csrf_token } = this.state;
    this.setState({ isEnable: 0, currentIndex: null, type: "" });
    windowParent.source.postMessage({ type: "closePopUp", iframeId, csrf_token }, windowParent.origin);
  };
  /* GET FEATURED POP UP FROM RECEIVED EVENTS FROM POST */
  getWidgetPopUPEvent = (event) => {
    if (event !== undefined && event.data != "" && event.data.card) {
      if (event.data.type == "post") {
        this.setState({
          platform: this.checkBrowser(),
          url: event.origin,
          isEnable: 1,
          eventData: event.data,
          currentIndex: event.data.index,
          completeData: event.data.idArray,
          viewOnText: event.data.viewOnText,
          shareText: event.data.shareText,
          windowParent: event,
          imgData: event.data.imgData,
          personalization: event.data.personalization,
          themeRule: event.data.ThemeRule,
          wall: event.data.wall,
          type: event.data.type,
          csrf_token: event.data.csrf_token
        })

      }
      else if (event.data.type == "submit-pic") {
        this.setState({
          isEnable: 1,
          UserRule: event.data.UserRule,
          wall: event.data.wall,
          type: event.data.type,
          windowParent: event,
          csrf_token: event.data.csrf_token,
          iframeId: event.data.iframeId
        })
      }


    }
  };
  checkBrowser = () => {
    let userAgentString = window.navigator.userAgent;
    if (userAgentString.indexOf("Chrome") > -1) return "tb-chrome modal-open";
    else if (userAgentString.indexOf("Safari") > -1) return "tb-safari modal-open";
    else return "modal-open";
  }
  /* POP SLIDER CHANGE INDEX WITH SLIDE */
  featuredPopSliderChangeIndex = (currentIndex) => event => {
    const { eventData } = this.state;
    if (currentIndex === -1) this.setState({ currentIndex: eventData.idArray.length - 1 });
    else this.setState({ currentIndex: eventData.idArray.length == currentIndex ? 0 : currentIndex });
  };
  SliderWithKey = (currentIndex) => {
    const { eventData } = this.state;
    if (currentIndex === -1) this.setState({ currentIndex: eventData.idArray.length - 1 });
    else this.setState({ currentIndex: eventData.idArray.length == currentIndex ? 0 : currentIndex });
  };
  componentDidMount() {
    WINDOW_ON_LOAD();
    document.body.className = this.checkBrowser();
    this.WidgetPopUPSlideWithArrowKey();



  }
  /* POP SLIDER WITH ARROW KEYS EVENTS */
  WidgetPopUPSlideWithArrowKey = e => {
    const { eventData, personalization } = this.state;

    if (personalization && personalization.popupSlideShow === 1) {
      e = e || window.event;
      let self = this;
      if (e && e.keyCode && e.keyCode != undefined) {
        if (e.keyCode == 37) self.SliderWithKey(self.state.currentIndex - 1)
        else if (e.keyCode == 39) self.SliderWithKey(self.state.currentIndex + 1)
      }
    }


  }
  onUpdateCsrfToken = (token) => {
    this.setState({ csrf_token: token })
  }

  render() {
    if (window.addEventListener) window.addEventListener("message", this.getWidgetPopUPEvent);
    const { viewOnText, isEnable, currentIndex, personalization, completeData, eventData, url, platform, imgData, themeRule, wall, type, csrf_token, windowParent, iframeId } = this.state;
    let data = null;
    if (currentIndex === -1) data = completeData[eventData.idArray.length - 1];
    else data = completeData[currentIndex];

    return (isEnable === 1 && wall && type == "submit-pic") ? <OnsiteUpload wall={wall} wallId={wall.Wall.id} onClosePopUp={this.closeWidgetPopUP} csrf_token={csrf_token} onUpdateCsrfToken={this.onUpdateCsrfToken} windowParent={windowParent} iframeId={iframeId} /> : (isEnable === 1 && type == "post" && themeRule && wall) ? (data.stories && data.stories == 1) ? <StoryPopup key={uuidv4()} completeData={completeData} eventData={eventData} wall={wall} item={data} onClosePopUp={this.closeWidgetPopUP} currentIndex={currentIndex} personalization={personalization} viewOnText={viewOnText} slideWithArrowKeys={this.WidgetPopUPSlideWithArrowKey} featuredPopSliderChangeIndex={this.featuredPopSliderChangeIndex} hostUrl={url} platform={platform} imgData={imgData} />
      : <WidgetPopUP completeData={completeData} eventData={eventData} themeRule={themeRule} wall={wall} item={data} onClosePopUp={this.closeWidgetPopUP} currentIndex={currentIndex} personalization={personalization} viewOnText={viewOnText} slideWithArrowKeys={this.WidgetPopUPSlideWithArrowKey} featuredPopSliderChangeIndex={this.featuredPopSliderChangeIndex} hostUrl={url} platform={platform} imgData={imgData} /> : null
  }
}
export default App;

