export default class MultiImageSlider extends Component {
    state = {
        activeSlide: 0,
        activeSlide2: 0
    };
    render() {
        const settings = {
            dots: true,
            dotsClass: "tb_slider_dots",
            infinite: true,
            speed: 500,
            centerMode: true,
            centerPadding: 0,
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 0,
            mobileFirst: true,
            adaptiveHeight: true,
            nextArrow: <NextArrow classNameCustom={'tb_next_arrow_00'} />,
            prevArrow: <PrevArrow classNameCustom={'tb_prev_arrow_00'} />,
            beforeChange: (current, next) => this.setState({ activeSlide: next }),
            afterChange: current => this.setState({ activeSlide2: current })
        };
        const { imageList, wall, item } = this.props;
        return (
            <div className="tb_multiple_slider01">
                <Slider {...settings}>
                    {
                        imageList.map((imageItem, index) => <div key={index} className="tb_multiple_slider00">
                            <div className="tb_slider_img_wrap00">
                                <img src={imageItem.postFile}
                                    alt={imageItem.id}
                                    data-link={imageItem.link}
                                    data-filter-id={item.filterId}
                                    data-load="0"
                                    data-network={imageItem.networkId}
                                    className="tb_img_blur__0" data-wall-id={wall.Wall.id} data-item-id={imageItem.id}
                                    data-tb={wall.UserDetail.db_table}
                                    onError={(e) => {
                                        IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e)
                                        //console.clear()
                                    }}
                                    height="1000"
                                    width="1000"
                                />
                                <div className="tb_slider_img_wrapp001">
                                    {
                                        (imageItem.type === 2 || imageItem.type === 4) ? <img
                                            src={imageItem.postFile}
                                            alt=""
                                            data-filter-id={item.filterId}
                                            data-link={imageItem.link} data-load="0" data-network={imageItem.networkId}
                                            data-type="image"
                                            data-stories={item.stories}
                                            data-tb={wall.UserDetail.db_table}
                                            // data-filter-id={item.filterId}
                                            // onLoad={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e)}
                                            className="tb-detail-image" data-wall-id={wall.Wall.id} data-item-id={imageItem.id}
                                            onError={(e) => {
                                                IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e)
                                                //console.clear()
                                            }}
                                            height="1000"
                                            width="1000"
                                        /> : (imageItem.type === 3 || imageItem.type === 5) && (index === this.state.activeSlide) &&
                                            (item.link.indexOf("youtube") >= 0 ||
                                                item.link.indexOf("youtu.be") >= 0 ||
                                                item.link.indexOf("vimeo") >= 0 ||
                                                item.link.indexOf("tumblr") >= 0 || item.link.indexOf("soundcloud") >= 0 || item.link.indexOf("linkedin") >= 0) ? (
                                                    <div className="tb_video_wrap">
                                                        <Iframe
                                                            className="tb-detail-image-iframe"
                                                            frameBorder="0"
                                                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                            src={`${imageItem.mediaFile}`}
                                                            allowFullScreen="true"
                                                            style={{ width: "100%!important" }}
                                                            data-filter-id={item.filterId}
                                                            data-stories={item.stories}
                                                            data-tb={wall.UserDetail.db_table}
                                                            onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }} data-type="video"
                                                            data-network={imageItem.networkId}
                                                            data-link={imageItem.link}
                                                            data-wall-id={wall.Wall.id} data-item-id={imageItem.id}
                                                            data-load={0}
                                                        ></Iframe>
                                                    </div>
                                                ) : (item.link.indexOf("facebook") >= 0) && (index === this.state.activeSlide) ? <div>
                                                    <ReactPlayer style={{ display: 'contents' }}
                                                        className="tb-detail-image-iframe" url={imageItem.mediaFile} controls onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }} data-type="video"
                                                        data-filter-id={item.filterId}
                                                        data-stories={item.stories}
                                                        data-tb={wall.UserDetail.db_table}
                                                        data-network={imageItem.networkId}
                                                        data-link={imageItem.link}
                                                        data-wall-id={wall.Wall.id} data-item-id={imageItem.id}
                                                        data-load={0}
                                                        autoPlay
                                                    /></div> : (
                                                        <div className="tb_video_wrap">
                                                            {(window.navigator.userAgent.includes('Safari') && !window.navigator.userAgent.includes('Chrome')) && imageItem.mediaFile.includes('instagram') ?
                                                                (index === this.state.activeSlide) ?
                                                                    <video
                                                                        data-wall-id={wall.Wall.id} data-item-id={imageItem.id}
                                                                        src={imageItem.mediaFile}
                                                                        data-filter-id={item.filterId}
                                                                        //  onError={this.onErrorVideo}
                                                                        data-stories={item.stories}
                                                                        data-tb={wall.UserDetail.db_table}
                                                                        onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                                                        autoPlay
                                                                        controls
                                                                        data-type="video"
                                                                        data-network={imageItem.networkId}
                                                                        data-link={imageItem.link}
                                                                        data-load={0}
                                                                        poster={imageItem.postFile}
                                                                        height="1000"
                                                                        width="1000"
                                                                    ></video> : ''

                                                                // <img data-wall-id={wall.Wall.id} data-item-id={imageItem.id}
                                                                //     src={imageItem.mediaFile}
                                                                //     data-filter-id={item.filterId}
                                                                //     //  onError={this.onErrorVideo}
                                                                //     onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                                                //     data-type="video"
                                                                //     data-network={imageItem.networkId}
                                                                //     data-link={item.link}
                                                                //     data-load={0}

                                                                // /> 

                                                                : index === this.state.activeSlide ? <video
                                                                    data-wall-id={wall.Wall.id} data-item-id={imageItem.id}
                                                                    src={imageItem.mediaFile}
                                                                    data-filter-id={item.filterId}
                                                                    //  onError={this.onErrorVideo}
                                                                    data-stories={item.stories}
                                                                    data-tb={wall.UserDetail.db_table}
                                                                    onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                                                    autoPlay
                                                                    controls
                                                                    data-type="video"
                                                                    data-network={imageItem.networkId}
                                                                    data-link={imageItem.link}
                                                                    data-load={0}
                                                                    poster={imageItem.postFile}
                                                                    height="1000"
                                                                    width="1000"
                                                                ></video> : null}
                                                        </div>
                                                    )
                                    }
                                    {
                                        item.network.id == 24 ? (
                                            <img
                                                src="img/loading.gif"
                                                alt="loading"
                                                className="tb-detail-image"
                                                style={{
                                                    visibility: "visible",
                                                    position: "relative",
                                                    transform: "translate(-50%, -50%)",
                                                    top: "50%",
                                                    left: "50%",
                                                    display: "none",
                                                    width: "60px",
                                                }}
                                            />
                                        ) : null
                                    }
                                </div>
                            </div>
                        </div>)
                    }
                </Slider>
            </div>
        );
    }
}

