import React, { Component } from 'react';
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'font-awesome/css/font-awesome.min.css';
import '../../css/components-rounded.min.css'
import '../../css/all-fonts.css'
import '../../css/feature-popup.css'
import '../../css/shop.css'
import '../../App.css'
import { v4 as uuidv4 } from 'uuid';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT } from '../../utils'
import { IMG_DATA_FILTER } from '../../common/constent'

import { isValidAuthorImage } from '../../utils';
import StoryPopupAuthor from './StoryPopupAuthor';
import Slider from "react-slick";
import './css/story-popup-style.css'
import StoryPerSlide from './storyPerSlide'

const fontColorIfWhite = (white, color, defaultColor, cardColor) => {
    if (cardColor.toLowerCase() === white) return white == color.toLowerCase() ? defaultColor : color;
    else if (cardColor.toLowerCase() === defaultColor) return defaultColor == color.toLowerCase() ? white : color.toLowerCase();
    return white == color ? defaultColor : color;
}

function InnerPrevArrow(props) {
    const { classNameCustom, style, onClick } = props;
    return (
        <div className={classNameCustom} onClick={onClick} />
    );
}


function InnerNextArrow(props) {
    const { classNameCustom, onClick } = props;
    return (
        <div className={classNameCustom} onClick={onClick} />
    );
}
var activeSlide = 0;
class StoryPopup extends Component {
    state = {
        currentIndex: this.props.currentIndex,
        item: this.props.item,
        isVideoError: false,
        windowWidth: window.innerWidth,
        instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link),
        updateDataArray: []
    }
    componentDidMount() {
        const { currentIndex, item } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ updateDataArray: Array.from(Array(item.imageList.length).keys()), currentIndex: currentIndex, item: item, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link) });
        setTimeout(() => {
            $(document).ready(function () {
                $(".tb_sp_nxt_btn").focus();
            });
        }, 1000)
        window.addEventListener("resize", () => {
            this.setState({ windowWidth: window.innerWidth })
        });
    }
    componentWillReceiveProps(nextProps) {
        const { currentIndex, item } = nextProps;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isVideoError: false, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(nextProps.imgData, nextProps.item.link) });
    }
    onErrorVideo = (e) => this.setState({ isVideoError: true })

    render() {

        //tb_sp_seen_slide
        const { personalization, onClosePopUp, featuredPopSliderChangeIndex, slideWithArrowKeys, hostUrl, viewOnText, platform, wall, eventData, completeData } = this.props;
        const { currentIndex, item, isVideoError, windowWidth, instaImg } = this.state;
        const redirectLink = (item.network.id == 18 || item.network.id == 2) ? `https://www.instagram.com/` : (item.network.id == 1) ? `https://twitter.com/` : ``
        const multiImage = true;
        const settingsinner = {
            dots: true,
            infinite: false,
            autoplay: true,
            autoplaySpeed: 4000,
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 0,
            mobileFirst: true,
            className: "tb_sp_slider_multiple00",
            dotsClass: "tb_sp_multiple_slide_pro",
            prevArrow: <InnerPrevArrow classNameCustom={'tb_sp_prev_arrow_in_00'} />,
            nextArrow: <InnerNextArrow classNameCustom={'tb_sp_next_arrow_in_00'} />,
            // beforeChange: (oldIndex, newIndex) => {
            //     console.log("before changes", oldIndex, newIndex)
            //     if (item.imageList && item.imageList.length > 0) {
            //         item.imageList.map((imageItem, itemIndex) => {
            //             var updateSlideClass = document.querySelector(`#slide_seen_index_update_${oldIndex}`)
            //             if (itemIndex < newIndex) {

            //                 console.log("updateSlideClass", updateSlideClass)
            //                 if (updateSlideClass) {
            //                     updateSlideClass.classList.add("tb_sp_seen_slide")
            //                 }
            //             }

            //             else {
            //                 updateSlideClass.classList.remove('tb_sp_seen_slide')
            //             }
            //         })

            //     }
            // },
            afterChange: (index) => {
                const { updateDataArray } = this.state;
                if (updateDataArray && updateDataArray.length > 0) {
                    updateDataArray.map((imageItem) => {
                        var updateSlideClass = document.querySelector(`#slide_seen_index_update_${parseInt(imageItem)}`)
                        if (parseInt(imageItem) < index && updateSlideClass) {
                            updateSlideClass.classList.add("tb_sp_seen_slide")
                            //console.log("parseInt", parseInt(imageItem), index)

                        }
                        else if ((parseInt(updateDataArray.length) - 1) == index) {
                            //console.log("parseInt(imageItem) == index", (parseInt(updateDataArray.length) - 1))
                            setTimeout(() => $(".tb_sp_nxt_btn").trigger("click"), 4000)
                        }
                        else {
                            updateSlideClass.classList.remove('tb_sp_seen_slide')
                        }
                    })

                }
            },
            customPaging: i => {
                return <div key={uuidv4()} className="tb_sp_multiple_slide_bar">
                    <div id={`slide_seen_index_update_${i}`} className={`tb_sp_slide_progress`} style={{ animationDuration: '5000ms' }} />
                </div>
            }

        };

        return <div key={uuidv4()} id="modalPost"
            className={`modalId${item.id} cstmModalPopup taggModalShow tb_sp_modal tb_sp_fade ${wall.UserRule.branding_lite && wall.UserRule.branding_lite == 1 ? `poweredbytaggbox__` : ``} in`
            }
            onKeyDown={slideWithArrowKeys}
            onKeyPress={slideWithArrowKeys}
            onLoad={slideWithArrowKeys}
        >
            <div className="tb_sp_modal_dialog">
                <div className="tb_sp_conetent">
                    <div class="tb_sp_modal_head">
                        <div class="tb_sp_modal_header">
                            <div class="tb_sp_close_wrap">
                                <div class="tb_sp_close_btn" onClick={onClosePopUp}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="12.728" height="12.728" viewBox="0 0 12.728 12.728">
                                        <g id="tb_cross__" transform="translate(-5.636 -5.636)">
                                            <path fill="#2b2b2b" id="tb_cross__00" data-name="Path 3" d="M1,0H15a1,1,0,0,1,0,2H1A1,1,0,0,1,1,0Z" transform="translate(5.636 16.95) rotate(-45)" />
                                            <path fill="#2b2b2b" id="tb_cross__11" data-name="Path 2" d="M1,0H15a1,1,0,0,1,0,2H1A1,1,0,0,1,1,0Z" transform="translate(7.05 5.636) rotate(45)" />
                                        </g>
                                    </svg>
                                </div>
                            </div>
                        </div>
                    </div>
                    {personalization.popupSlideShow == 1 ? (<>
                        <div className="tb_sp_nav tb_sp_prev_btn" onClick={featuredPopSliderChangeIndex(currentIndex - 1)}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24">
                                <g id="Group_4485" data-name="Group 4485" transform="translate(10922 -4712)">
                                    <g id="Group" transform="translate(-10948.406 4695.088)">
                                        <path id="Path" d="M42.129,21.333a1.094,1.094,0,0,0-1.559,0l-6.837,6.837a1.094,1.094,0,0,0,0,1.559l6.837,6.837a1.1,1.1,0,1,0,1.559-1.559l-6.044-6.072,6.044-6.072A1.094,1.094,0,0,0,42.129,21.333Z" fill="#545454"></path>
                                    </g>
                                </g>
                            </svg>
                        </div>
                        <div className="tb_sp_nav tb_sp_nxt_btn" onClick={featuredPopSliderChangeIndex(currentIndex + 1)}>
                            <svg id="Group_4485" data-name="Group 4485" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                <g id="Group" transform="translate(7.954 4.094)">
                                    <path id="Path" d="M33.729,21.333a1.094,1.094,0,0,1,1.559,0l6.837,6.837a1.094,1.094,0,0,1,0,1.559l-6.837,6.837a1.1,1.1,0,0,1-1.559-1.559l6.044-6.072-6.044-6.072a1.094,1.094,0,0,1,0-1.53Z" transform="translate(-33.407 -21.007)" fill="#545454"></path>
                                </g>
                            </svg>
                        </div>
                    </>) : null}
                    <div class="tb_sp_modal_body">
                        <div class="tb_sp_details_left">
                            <div class="tb_sp_media_holder">
                                <div class="tb_sp_media_wrapper_in ">
                                    {(item.imageList && item.imageList.length > 0) ?
                                        <Slider {...settingsinner} ref={c => (this.slider = c)} key={uuidv4()}>
                                            {item.imageList.map((imageItem, index) => <div key={uuidv4()}>
                                                <div class="tb_sp_img_holder_wrap">
                                                    <StoryPopupAuthor {...item} wall={wall} />
                                                    <div class="tb_sp_img_holder">
                                                        {item.type === 2 || item.type === 4 ? <>
                                                            <img src={item.filename} alt={item.id} className="tb_sp_img" />
                                                        </> :
                                                            item.type === 1 ? <></>
                                                                : (item.type === 3 || item.type === 5) &&
                                                                    (item.link.indexOf("youtube") >= 0 ||
                                                                        item.link.indexOf("youtu.be") >= 0 ||
                                                                        item.link.indexOf("vimeo") >= 0 ||
                                                                        item.link.indexOf("tumblr") >= 0 || item.link.indexOf("soundcloud") >= 0 || item.link.indexOf("linkedin") >= 0) ? <>
                                                                        <Iframe
                                                                            className="tb_sp_img"
                                                                            frameBorder="0"
                                                                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                                            src={`${item.mediaUrl}`}
                                                                            allowFullScreen="true"
                                                                            style={{ width: "100%!important" }}
                                                                        ></Iframe>
                                                                    </>
                                                                    : (item.link.indexOf("facebook") >= 0) ? <><ReactPlayer className="tb_sp_img" style={{ display: 'contents' }} url={item.mediaUrl} controls autoPlay />
                                                                    </> : <video
                                                                        className="tb_sp_img"
                                                                        data-filter-id={item.filterId}
                                                                        src={item.mediaUrl}
                                                                        autoPlay
                                                                        controls
                                                                    ></video>
                                                        }
                                                    </div>
                                                </div>
                                            </div>
                                            )}
                                        </Slider>
                                        :
                                        <StoryPerSlide item={item} />
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    }
}
export default StoryPopup;