export const capitalize = (s) => {
    if (typeof s !== "string") return "";
    return s.charAt(0).toUpperCase() + s.slice(1);
};

export const WINDOW_ON_LOAD = () => {
    window.onload = () => {
        ADD_SCRIPT("https://widget.taggbox.com/common-assets/js/jquery.min.js")
        ADD_SCRIPT("https://widget.taggbox.com/common-assets/js/slackdown.js")
    }
}

export const ADD_SCRIPT = url => {
    const script = document.createElement("script")
    script.src = url
    script.async = true
    document.body.appendChild(script)
}
export const NO_CAMERA_IMAGE = `https://cloud.taggbox.com/widget/widget-embed/no-camera.svg`;
export const IMG_DATA_FILTER = (data, link) => {
    if (data && data.length > 0) {
        return data.filter((item) => item.link == link)
    }
    else return [];
}

