const convertObjectToArray = (obj) => {
    if (Object.keys(obj)) {
        let result = Object.keys(obj).map((key) => obj[key]);
        return result;
    }
    else return [];

}
//const fontColorIfWhite = (white, color, defaultColor) => white == color ? defaultColor : color;
const fontColorIfWhite = (white, color, defaultColor, cardColor, widgetTheme) => {
    const applyStyle = [4, 16, 50, 52, 55].includes(widgetTheme);
    if (cardColor.toLowerCase() === white) return white == color.toLowerCase() && applyStyle ? defaultColor : color;
    else if (cardColor.toLowerCase() === defaultColor) return defaultColor == color.toLowerCase() && applyStyle ? white : color.toLowerCase();
    return white == color && applyStyle ? defaultColor : color;
}
