    componentDidMount() {
        const { currentIndex, item, wall } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link) });
        setTimeout(() => {
            $(document).ready(function () {
                $(".tb_nxt_btn").focus();
            });
        }, 1000)
        window.addEventListener("resize", () => {
            this.setState({ windowWidth: window.innerWidth })
        });
        if (wall.Wall.owner === 23390) {
            const focusableElements =
                'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])';
            const modal = document.querySelector('#modalPost'); // select the modal by it's id
            const firstFocusableElement = modal.querySelectorAll(focusableElements)[0]; // get first element to be focused inside modal
            const focusableContent = modal.querySelectorAll(focusableElements);
            const lastFocusableElement = focusableContent[focusableContent.length - 1]; // get last element to be focused inside modal


            modal.addEventListener('keydown', function (e) {
                let isTabPressed = e.key === 'Tab' || e.keyCode === 9;

                if (!isTabPressed) {
                    return;
                }

                if (e.shiftKey) { // if shift key pressed for shift + tab combination
                    if (document.activeElement === firstFocusableElement) {
                        lastFocusableElement.focus(); // add focus for the last focusable element
                        e.preventDefault();
                    }
                } else { // if tab key is pressed
                    if (document.activeElement.getAttribute("href") === lastFocusableElement.getAttribute("href")) { // if focused has reached to last focusable element then focus first focusable element after pressing tab
                        modal.querySelector(".tb_prev_btn").focus();
                        firstFocusableElement.focus(); // add focus for the first focusable element
                        e.preventDefault();
                    }
                }
            });
            firstFocusableElement.focus();
        }
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
    }
    componentWillReceiveProps(nextProps) {
        const { currentIndex, item } = nextProps;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isVideoError: false, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(nextProps.imgData, item.link) });
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
    }

