import React, { Component } from "react";
import ReactHtmlParser from 'react-html-parser';
import { STRING_TO_URL_CONVERT } from '../utils'
const reactStringReplace = require("react-string-replace");
const convertCss = (hashtag_color, match, isHash, hashtag_background_color) => {
    let style = `<span className="gridhashtag" style="color:${hashtag_color};font-weight:bold;background-color:${hashtag_background_color};">${isHash ? `#` : ``}${match}</span>`
    return style;
}

const SlackContent = ({ fdata, content, personalization, unicodeyEmojiConvert }) => <React.Fragment>

    {personalization.hashtag_highlight === 1 &&
        personalization.hashtag_feed === 0 &&
        personalization.hashtag_all === 0
        ? unicodeyEmojiConvert(content)
        : personalization.hashtag_highlight === 1 &&
            personalization.hashtag_feed === 0 &&
            personalization.hashtag_all === 1
            ? unicodeyEmojiConvert(reactStringReplace(
                content,
                /#(\w+)/g,
                (match, i) => convertCss(personalization.hashtag_color, match, true, personalization.hashtag_background_status == 1 ? personalization.hashtag_background_color : 'transparent')
            ))
            : personalization.hashtag_highlight === 1 &&
                personalization.hashtag_feed === 1 &&
                personalization.hashtag_all === 0 && fdata.hash.hashString != ""
                ? unicodeyEmojiConvert(reactStringReplace(
                    content,
                    fdata.hash.hashString,
                    (match, i) => convertCss(personalization.hashtag_color, match, false, personalization.hashtag_background_status == 1 ? personalization.hashtag_background_color : 'transparent')
                ))
                : personalization.hashtag_highlight === 1 &&
                    personalization.hashtag_feed === 1 &&
                    personalization.hashtag_all === 1
                    ? unicodeyEmojiConvert(reactStringReplace(
                        content,
                        /#(\w+)/g,
                        (match, i) => convertCss(personalization.hashtag_color, match, true, personalization.hashtag_background_status == 1 ? personalization.hashtag_background_color : 'transparent')
                    ))
                    : unicodeyEmojiConvert(content)}
</React.Fragment>

const ContentWithOutSlack = ({ fdata, content, personalization, unicodeyEmojiConvert }) => <React.Fragment>
    {personalization.hashtag_highlight === 1 &&
        personalization.hashtag_feed === 0 &&
        personalization.hashtag_all === 0
        ? unicodeyEmojiConvert(ReactHtmlParser(content))
        : personalization.hashtag_highlight === 1 &&
            personalization.hashtag_feed === 0 &&
            personalization.hashtag_all === 1
            ? unicodeyEmojiConvert(reactStringReplace(
                ReactHtmlParser(content),
                /#(\w+)/g,
                (match, i) => (
                    <span
                        className="gridhashtag"
                        style={{
                            color: personalization.hashtag_color,
                            fontWeight: "bold",
                            backgroundColor: personalization.hashtag_background_status == 1 ? personalization.hashtag_background_color : 'transparent'
                        }}
                    >

                        #{match}
                    </span>
                )
            ))
            : personalization.hashtag_highlight === 1 &&
                personalization.hashtag_feed === 1 &&
                personalization.hashtag_all === 0 && fdata.hash.hashString != ""
                ? unicodeyEmojiConvert(reactStringReplace(
                    ReactHtmlParser(content),
                    fdata.hash.hashString,
                    (match, i) => (
                        <span
                            className="gridhashtag"
                            style={{
                                color: personalization.hashtag_color,
                                fontWeight: "bold",
                                backgroundColor: personalization.hashtag_background_status == 1 ? personalization.hashtag_background_color : 'transparent'
                            }}
                        >

                            {match}
                        </span>
                    )
                ))
                : personalization.hashtag_highlight === 1 &&
                    personalization.hashtag_feed === 1 &&
                    personalization.hashtag_all === 1
                    ? unicodeyEmojiConvert(reactStringReplace(
                        ReactHtmlParser(content),
                        /#(\w+)/g,
                        (match, i) => (
                            <span key={i}
                                className="gridhashtag"
                                style={{
                                    color: personalization.hashtag_color,
                                    fontWeight: "bold",
                                    backgroundColor: personalization.hashtag_background_status == 1 ? personalization.hashtag_background_color : 'transparent'
                                }}
                            >

                                #{match}
                            </span>
                        )
                    ))
                    : unicodeyEmojiConvert(ReactHtmlParser(content))}
</React.Fragment>

const findHaseTagIndex = (data) => {
    let FindHasheTag = [];
    for (var i = 0; i < data.length; i++) {
        if (data[i] === "#") FindHasheTag.push(i);
    }
    return FindHasheTag;
}
const findWhiteSpace = (startFrom, content) => {
    let value = 0;

    for (; startFrom < content.length; startFrom++) {
        if (content[startFrom] == " " || content[startFrom] == '') {
            return value = startFrom; break;
        }
    }
    if (value === 0 && content.length < 210) value = content.length;
    else value = 200;
    return startFrom == undefined ? 200 : value;

}

class ContentConversion extends Component {
    state = {
        isTrimEnable: false
    }
    unicodeyEmojiConvert = (data) => {
        return <>{data}</>
    }
    unicodeyEmoji = (data) => {
        return <>{ReactHtmlParser(data)}</>
    }


    onClickReadText = event => {
        const { isTrimEnable } = this.state;
        this.setState({ isTrimEnable: !isTrimEnable });
    }

    render() {
        const { fdata, contentData, fullcontentData, personalization } = this.props;
        var content = STRING_TO_URL_CONVERT(fdata.content);
        let trimContent = 0;
        // let content = fdata.content;
        let hashTagFindContent = ""; let fullcontent = fdata.fullcontent;
        if (fdata.network.id != 20 && fdata.content.length > 200) {
            hashTagFindContent = String(contentData).substring(0, 200);
            let lastHashTag = findHaseTagIndex(hashTagFindContent);
            let hashTagLimit = findWhiteSpace(lastHashTag[lastHashTag.length - 1], fdata.content)
            content = STRING_TO_URL_CONVERT(String(contentData).substring(0, parseInt(hashTagLimit)));
            fullcontent = STRING_TO_URL_CONVERT(String(fullcontentData).substring(parseInt(hashTagLimit), fdata.content.length));

        }
        else {
            content = STRING_TO_URL_CONVERT(fdata.content);
            fullcontent = STRING_TO_URL_CONVERT(fdata.content);
        }

        return fdata.network.id === 20 ? <React.Fragment><SlackContent fdata={fdata} content={window.slackdown.parse(fdata.content, fdata.slackMember)} personalization={personalization} unicodeyEmojiConvert={this.unicodeyEmoji} />

            {
                fdata.content.length > 200 ? <span className="morecontent readMore"><span>{this.state.isTrimEnable ? <SlackContent fdata={fdata} content={window.slackdown.parse(fullcontent)} personalization={personalization} unicodeyEmojiConvert={this.unicodeyEmoji} /> : null}
                    {this.state.isTrimEnable ? <a className="lesslink" onClick={this.onClickReadText}> Read Less</a> : null}</span>
                    {!this.state.isTrimEnable ? <a className="morelink" onClick={this.onClickReadText}> Read More...</a> : null}</span> : null
            }


        </React.Fragment> : <React.Fragment><ContentWithOutSlack fdata={fdata} content={content} personalization={personalization} unicodeyEmojiConvert={this.unicodeyEmojiConvert} />

                {
                    fdata.content.length > 200 ? <span className="morecontent readMore"><span>{this.state.isTrimEnable ? <ContentWithOutSlack fdata={fdata} content={fullcontent} personalization={personalization} unicodeyEmojiConvert={this.unicodeyEmojiConvert} /> : null}
                        {this.state.isTrimEnable ? <a className="lesslink" onClick={this.onClickReadText}> Read Less</a> : null}</span>
                        {!this.state.isTrimEnable ? <a className="morelink" onClick={this.onClickReadText}> Read More...</a> : null}</span> : null
                }

            </React.Fragment>


    }

}

export default ContentConversion;