import React, { Component } from 'react';
import { Helmet } from "react-helmet";
import { CloudUrl } from '../utils';

const PageHeadSection = ({ personalization, themeRule }) => <>
    {<style>
    {`@font-face {
        font-family: 'TaggboxSocialFonts';
        src:  url('${CloudUrl}/web-fonts/TaggboxIcon.eot?jkob7x');
        src:  url('${CloudUrl}/web-fonts/TaggboxIcon.eot?jkob7x#iefix') format('embedded-opentype'),
            url('${CloudUrl}/web-fonts/TaggboxIcon.ttf?jkob7x') format('truetype'),
            url('${CloudUrl}/web-fonts/TaggboxIcon.woff?jkob7x') format('woff'),
            url('${CloudUrl}/web-fonts/TaggboxIcon.svg?jkob7x#TaggboxSocialFonts') format('svg');
        font-weight: normal;
        font-style: normal;
        font-display: block;
    }`}
    </style>
    }
    {
        personalization.cssStatus === 1 && <style type="text/css">{personalization.css}</style>
    }
    {
        themeRule && Object.keys(themeRule).length > 0 ? <link href={`https://fonts.googleapis.com/css?family=${themeRule.link_font}:${themeRule.font_varient}`} rel="stylesheet" /> : null
    }

</>
