import React, { Component } from 'react';
import $ from 'jquery';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import StoryPopupAuthor from './StoryPopupAuthor';
class StoryPerSlide extends Component {
    state = {
        animationDuration: '5000ms'
    }
    componentWillMount() {
        const { item } = this.props;
        if (!(item.type === 3 || item.type === 5)) {
        }
    }
    updateDuration = () => {
        const { item } = this.props;
        var video = document.querySelector(`#video_id_${item.id}`);
        var updateProgress = document.querySelector(`#tb_sp_slide_progress_animation_${item.id}`)
        if (video.readyState) {
            if (video.duration > 0) {
                var seconds = video.duration % 60;
                seconds = parseInt(seconds) * 1000;
                if (updateProgress) {
                    updateProgress.style.animationDuration = `${seconds}ms`
                    updateProgress.addEventListener('animationend', () => $(".tb_sp_nxt_btn").trigger("click"));
                }
            }
        }
    }
    updateImageAnimation = (e) => {
        const { item } = this.props;
        var updateProgress = document.querySelector(`#tb_sp_slide_progress_animation_${item.id}`)
        if (updateProgress) {
            updateProgress.style.animationDuration = `5000ms`
            updateProgress.addEventListener('animationend', () => $(".tb_sp_nxt_btn").trigger("click"));
        }
    }
    onAnimationPaused = event => {
        const { item } = this.props;
        var updateProgress = document.querySelector(`#tb_sp_slide_progress_animation_${item.id}`)
        var vid = document.querySelector(`#video_id_${item.id}`);
        if (updateProgress) {
            updateProgress.style.animationPlayState = "paused"
            if (vid) {
                vid.pause();
            }
        }
    }
    onAnimationON = event => {
        const { item } = this.props;
        var updateProgress = document.querySelector(`#tb_sp_slide_progress_animation_${item.id}`)
        var vid = document.querySelector(`#video_id_${item.id}`);
        var tb_sp_share_list_container = document.querySelector(`#tb_sp_share_list_container`)
        if (updateProgress) {
            updateProgress.style.animationPlayState = "running"
            if (tb_sp_share_list_container) {
                if (tb_sp_share_list_container.classList.contains("tb_sp_toggle")) {
                    tb_sp_share_list_container.classList.remove('tb_sp_toggle');
                }
            }
            if (vid) {
                vid.play();
            }
        }
    }

    render() {
        const { item } = this.props;
        const { animationDuration } = this.state;
        return <>
            <div className="tb_sp_multiple_slide_pro">
                <div className="tb_sp_multiple_slide_bar slick-active">
                    <div id={`tb_sp_slide_progress_animation_${item.id}`} className="tb_sp_slide_progress" />
                </div>
            </div>
            <div>
                <div class="tb_sp_img_holder_wrap">
                    <StoryPopupAuthor  {...item} onAnimationPaused={this.onAnimationPaused} onAnimationON={this.onAnimationON} />
                    <div class="tb_sp_img_holder">
                        {item.type === 2 || item.type === 4 ? <>
                            <img src={item.filename} alt={item.id} className="tb_sp_img" onLoad={this.updateImageAnimation} onMouseDown={this.onAnimationPaused}
                                onMouseUp={this.onAnimationON} />
                        </> :
                            item.type === 1 ? <></>
                                : (item.type === 3 || item.type === 5) &&
                                    (item.link.indexOf("youtube") >= 0 ||
                                        item.link.indexOf("youtu.be") >= 0 ||
                                        item.link.indexOf("vimeo") >= 0 ||
                                        item.link.indexOf("tumblr") >= 0 || item.link.indexOf("soundcloud") >= 0 || item.link.indexOf("linkedin") >= 0) ? <>
                                        <Iframe
                                            className="tb_sp_img"
                                            frameBorder="0"
                                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                            src={`${item.mediaUrl}`}
                                            allowFullScreen="true"
                                            style={{ width: "100%!important" }}
                                        ></Iframe>
                                    </>
                                    : (item.link.indexOf("facebook") >= 0) ? <><ReactPlayer className="tb_sp_img" style={{ display: 'contents' }} url={item.mediaUrl} controls autoPlay />
                                    </> : <video
                                        onMouseDown={this.onAnimationPaused}
                                        onMouseUp={this.onAnimationON}
                                        id={`video_id_${item.id}`}
                                        className="tb_sp_img"
                                        src={item.mediaUrl}
                                        autoPlay
                                        onDurationChange={this.updateDuration}
                                    ></video>
                        }
                    </div>
                </div>
            </div>
        </>
    }

}

export default StoryPerSlide;